                </FormItem>
                <FormItem>
                    <div slot="label" class="label">公司头像</div>
                    <Input v-model="form.head_portrait"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">公司图片墙</div>
                    <Input v-model="form.photo_wall_list"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">法人代表</div>
                    <Input v-model="form.corporate_representative"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">店铺主推产品</div>
                    <Input v-model="form.main_commodity_list"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">产品目录分类</div>
