<!--                    <Input v-model="form.commodity_list"></Input>-->
<!--                </FormItem>-->
                <FormItem
                   v-for="(item, index) in form.productResponseList"
                   :key="index"
                   :label="'商品' + (index + 1)">
                    <Row>
                        <Col span="8">
                            <Input type="text" v-model="item.title" placeholder="商品名称"></Input>&nbsp;&nbsp;&nbsp;
                        </Col>
                        <Col span="8">
                            <Input type="text" v-model="item.imgUrl" placeholder="商品图片地址"></Input>&nbsp;&nbsp;&nbsp;
                        </Col>
                        <Col span="8">
                            <Input type="text" v-model="item.price" placeholder="商品价格"></Input>&nbsp;&nbsp;&nbsp;
                        </Col>
                        <Col span="8">
                            <Input type="text" v-model="item.classify" placeholder="商品所属分类"></Input>&nbsp;&nbsp;&nbsp;
                        </Col>
                        <Col span="8" offset="1">
                            <Button @click="handleRemove(index)">Delete</Button>
