                </FormItem>
                <FormItem>
                    <div slot="label" class="label">公司详情</div>
                    <Input v-model="form.company_introduce"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">成立时间</div>
                    <Input v-model="form.date_establishment"></Input>
                </FormItem>
                <FormItem>
