                </FormItem>
                <FormItem>
                    <div slot="label" class="label">登记机关</div>
                    <Input v-model="form.registration_authority "></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">经营状态</div>
                    <Input v-model="form.state"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">企业信用代码</div>
                    <Input v-model="form.credit_code"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">企业类型</div>
                    <Input v-model="form.enterprise_type"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">销售品牌</div>
                    <Input v-model="form.brand"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">客户动态</div>
                    <Input v-model="form.dynamic_condition_list"></Input>
                </FormItem>
                <FormItem>
