                </FormItem>
                <FormItem>
                    <div slot="label" class="label">年检日期</div>
                    <Input v-model="form.annual_inspection_date"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">所属行业</div>
                    <Input v-model="form.industry_type"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">经营范围</div>
                    <Input v-model="form.brand_type"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">联系人名称</div>
                    <Input v-model="form.contact_information"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">电子邮箱</div>
                    <Input v-model="form.mailbox"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">qq</div>
                    <Input v-model="form.qq"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">微信</div>
                    <Input v-model="form.wx"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">电话</div>
                    <Input v-model="form.iphone"></Input>
                </FormItem>
                <FormItem>
                    <div slot="label" class="label">地址</div>
                    <Input v-model="form.site"></Input>
                </FormItem>
                <FormItem>
                    <Button type="primary" @click="handleSubmit('jsonForm')">生成</Button>
                    <Button style="margin-left: 20px;" @click="empty">清空</Button>
                </FormItem>
            </Form>
        </div>
        <json-model ref="jsonmodel" />
    </div>
</template>

<script>
import jsonModel from './jsonModel'
    export default {
        name: 'json',
        components: {jsonModel},
        data () {
            return {
                form: {
                    productResponseList: [
                        {
                            imgUrl: '',
                            title: '',
                            price: '',
                            classify: ''
                        }
                    ]
                },
                jsonObj: {}
            }
        },
        methods: {
            handleAdd () {
                this.index++;
                this.form.productResponseList.push({
                    imgUrl: '',
                    title: '',
                    price: '',
                    classify: ''
                });
            },
            handleRemove (index) {
                this.form.productResponseList.splice(index, 1)
            },
            handleSubmit (name) {
                this.$refs[name].validate(valid => {
                    return valid;
                }).then(res => {
                    // this.jsonObj = {
                    //     header: this.form.header,
                    //     title: this.form.title,
                    //     titleCon: this.form.titleCon,
                    //     content: this.form.content,
                    //     code: this.form.code,
                    //     conBody: this.form.conBody,
                    // }
                    // let json = this.jsonObj;
                    // this.$refs.jsonmodel.handle(json)
                    console.log('form', this.form)
                })
            },
            empty () {
                this.form = {
                    productResponseList: [
                        {
                            imgUrl: '',
                            title: '',
                            price: '',
                            classify: ''
                        }
                    ]
                }
            }
        },
        created () {
            // this.$axios.get('http://localhost:8080/json.json').then(res => {
            //     console.log('then')
            //     console.log(res)
            // }).catch(err => {
            //     console.log('catch')
            //     console.log(err)
            // })
        }
    }
</script>

<style scoped>
.json {
    width: 800px;
    margin: 20px auto;
}
.json .title {
    width: 100%;
    font-size: 32px;
    text-align: center;
    margin-bottom: 30px;
}
.json .label {
    width: 100px;
    text-align: right;
    padding-right: 10px;
}
</style>
